*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navBar{
  background-color: rgb(88, 179, 179) !important;
}
.navbar-brand{
    color: rgb(255, 255, 255) !important;
}
.navbar a{
  color: rgb(255, 255, 255) !important;
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font: bold;
  border-bottom: 1px solid rgb(0, 0, 0);
}
.navbar a:hover{
  color: black !important;
}
.get-started-btn {
  color: white;
text-decoration: none;
  letter-spacing: 1px;
  display: inline-block;
  background-color: rgb(127, 127, 209);
  border:2px solid rgb(4, 0, 255);
  padding: 10px 30px;
  border-radius: 50px;
  font-weight: 500;
  font-size: 16px;
  transition: 0.5s;
}
.get-started-btn:hover {
  color: rgb(0, 0, 0);
text-decoration: none;
  
  background-color: rgb(255, 255, 255);
  border:2px solid rgb(4, 0, 255);
  padding: 10px;
  border-radius: 25px;
}
.brand-name{
  color: rgb(13, 163, 163);
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header h2{
  color: grey;
  font-size: 24px;
}
.header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s  ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
.contact_div{
  width: 100%;
  height: 75vh;
}
footer p{
  padding: 0;
  margin: 0;
}
.card-img-top{
  height: 40vh;
}
